.task {
    margin: 0 auto;
    margin-top: 5px;
    min-height: 100px;
    width: 800px;
    font-size: 20px;
    border-radius: 15px;
}
.task .title {
    font-size: 22px;
    font-weight: bold;
}

.task:hover {
    cursor: pointer;
    border: solid 1px green;
    position: relative;
}
.task:hover .editControls {
    display: inline;
    position: absolute;
    right: 10px;
    top: 10px;
}
.task .editControls {
    display: none;
}
.task img{
    height: 30px;
    width: 30px;
    float: right;
}

.hidden {
    display: none;
}

/* .active {
    display: block;
} */


.button-wrap .active {
    display: block;
    width: 200px;
    left: 0;
    top: 0;
    position: absolute;
    background-color: white;
}
.priority1{
    border: solid 1px rgb(0, 255, 21);
}

.priority2{
    border: solid 1px rgb(55, 0, 255);
}

.priority3{
    border: solid 1px rgb(255, 0, 21);
}

.progressBarWrap {
    height: 8px;
    width: 95%;
    margin: auto;

}
.progressBarWrap .progressBar {
    background: blue;
    width: 0;
    -webkit-transition: width 1s ease-in-out;
    height: 100%;
}